      // data provided php side
      return this.$store.getters.getSubadminGroups;
    },
    quotaOptions() {
      // convert the preset array into objects
      const quotaPreset = this.settings.quotaPreset.reduce((acc, cur) => acc.concat({
        id: cur,
        label: cur
      }), []);
      // add default presets
      if (this.settings.allowUnlimitedQuota) {
        quotaPreset.unshift(this.unlimitedQuota);
      }
      quotaPreset.unshift(this.defaultQuota);
      return quotaPreset;
    },
    minPasswordLength() {
      return this.$store.getters.getPasswordPolicyMinLength;
    },
    usersOffset() {
      return this.$store.getters.getUsersOffset;
    },
    usersLimit() {
      return this.$store.getters.getUsersLimit;
    },
    usersCount() {
      return this.users.length;
    },
    /* LANGUAGES */
    languages() {
      return [{
        label: t('settings', 'Common languages'),
        languages: this.settings.languages.commonLanguages
      }, {
        label: t('settings', 'Other languages'),
        languages: this.settings.languages.otherLanguages
      }];
    },
    isDarkTheme() {
      return window.getComputedStyle(this.$el).getPropertyValue('--background-invert-if-dark') === 'invert(100%)';
    }
  },
  watch: {
    // watch url change and group select
    selectedGroup(val, old) {
      // if selected is the disabled group but it's empty
      this.redirectIfDisabled();
      this.$store.commit('resetUsers');
      this.$refs.infiniteLoading.stateChanger.reset();
      this.setNewUserDefaultGroup(val);
    },
    // make sure the infiniteLoading state is changed if we manually
    // add/remove data from the store
    usersCount(val, old) {
      // deleting the last user, reset the list
      if (val === 0 && old === 1) {
        this.$refs.infiniteLoading.stateChanger.reset();
        // adding the first user, warn the infiniteLoader that
        // the list is not empty anymore (we don't fetch the newly
        // added user as we already have all the info we need)
      } else if (val === 1 && old === 0) {
        this.$refs.infiniteLoading.stateChanger.loaded();
      }
    }
  },
  async beforeMount() {
    await this.searchUserManager();
  },
  mounted() {
    if (!this.settings.canChangePassword) {
      OC.Notification.showTemporary(t('settings', 'Password change is disabled because the master key is disabled'));
    }

    /**
     * Reset and init new user form
     */
    this.resetForm();

    /**
     * Register search
     */
    subscribe('nextcloud:unified-search.search', this.search);
    subscribe('nextcloud:unified-search.reset', this.resetSearch);

    /**
     * If disabled group but empty, redirect
     */
    this.redirectIfDisabled();
  },
  beforeDestroy() {
    unsubscribe('nextcloud:unified-search.search', this.search);
    unsubscribe('nextcloud:unified-search.reset', this.resetSearch);
  },
  methods: {
    async searchUserManager(query) {
      await this.$store.dispatch('searchUsers', {
        offset: 0,
        limit: 10,
        search: query
      }).then(response => {
        const users = response !== null && response !== void 0 && response.data ? Object.values(response === null || response === void 0 ? void 0 : response.data.ocs.data.users) : [];
        if (users.length > 0) {
          this.possibleManagers = users;
        }
      });
    },
    onScroll(event) {
      this.scrolled = event.target.scrollTo > 0;
    },
    /**
     * Validate quota string to make sure it's a valid human file size
     *
     * @param {string} quota Quota in readable format '5 GB'
     * @return {object}
     */
    validateQuota(quota) {
      // only used for new presets sent through @Tag
      const validQuota = OC.Util.computerFileSize(quota);
      if (validQuota !== null && validQuota >= 0) {
        // unify format output
        quota = OC.Util.humanFileSize(OC.Util.computerFileSize(quota));
        this.newUser.quota = {
          id: quota,
          label: quota
        };
        return this.newUser.quota;
      }
      // Default is unlimited
      this.newUser.quota = this.quotaOptions[0];
      return this.quotaOptions[0];
    },
    infiniteHandler($state) {
      this.$store.dispatch('getUsers', {
        offset: this.usersOffset,
        limit: this.usersLimit,
        group: this.selectedGroup !== 'disabled' ? this.selectedGroup : '',
        search: this.searchQuery
      }).then(usersCount => {
        if (usersCount > 0) {
          $state.loaded();
        }
        if (usersCount < this.usersLimit) {
          $state.complete();
        }
      });
    },
    /* SEARCH */
    search(_ref) {
      let {
        query
      } = _ref;
      this.searchQuery = query;
      this.$store.commit('resetUsers');
      this.$refs.infiniteLoading.stateChanger.reset();
    },
    resetSearch() {
      this.search({
        query: ''
      });
    },
    resetForm() {
      // revert form to original state
      this.newUser = Object.assign({}, newUser);

      /**
       * Init default language from server data. The use of this.settings
       * requires a computed variable, which break the v-model binding of the form,
       * this is a much easier solution than getter and setter on a computed var
       */
      if (this.settings.defaultLanguage) {
        Vue.set(this.newUser.language, 'code', this.settings.defaultLanguage);
      }

      /**
       * In case the user directly loaded the user list within a group
       * the watch won't be triggered. We need to initialize it.
       */
      this.setNewUserDefaultGroup(this.selectedGroup);
      this.loading.all = false;
    },
    createUser() {
      this.loading.all = true;
      this.$store.dispatch('addUser', {
        userid: this.newUser.id,
        password: this.newUser.password,
        displayName: this.newUser.displayName,
        email: this.newUser.mailAddress,
        groups: this.newUser.groups.map(group => group.id),
        subadmin: this.newUser.subAdminsGroups.map(group => group.id),
        quota: this.newUser.quota.id,
        language: this.newUser.language.code,
        manager: this.newUser.manager.id
      }).then(() => {
        this.resetForm();
        this.$refs.newusername.focus();
        this.closeModal();
      }).catch(error => {
        this.loading.all = false;
        if (error.response && error.response.data && error.response.data.ocs && error.response.data.ocs.meta) {
          const statuscode = error.response.data.ocs.meta.statuscode;
          if (statuscode === 102) {
            // wrong username
            this.$refs.newusername.focus();
          } else if (statuscode === 107) {
            // wrong password
            this.$refs.newuserpassword.focus();
          }
        }
      });
    },
    setNewUserDefaultGroup(value) {
      if (value && value.length > 0) {
        // setting new user default group to the current selected one
        const currentGroup = this.groups.find(group => group.id === value);
        if (currentGroup) {
          this.newUser.groups = [currentGroup];
          return;
        }
      }
      // fallback, empty selected group
      this.newUser.groups = [];
    },
    /**
     * Create a new group
     *
     * @param {string} gid Group id
     * @return {Promise}
     */
    createGroup(gid) {
      this.loading.groups = true;
      this.$store.dispatch('addGroup', gid).then(group => {
        this.newUser.groups.push(this.groups.find(group => group.id === gid));
        this.loading.groups = false;
      }).catch(() => {
        this.loading.groups = false;
      });
      return this.$store.getters.getGroups[this.groups.length];
    },
    /**
     * If the selected group is the disabled group but the count is 0
     * redirect to the all users page.
     * we only check for 0 because we don't have the count on ldap
     * and we therefore set the usercount to -1 in this specific case
     */
    redirectIfDisabled() {
      const allGroups = this.$store.getters.getGroups;
      if (this.selectedGroup === 'disabled' && allGroups.findIndex(group => group.id === 'disabled' && group.usercount === 0) > -1) {
        // disabled group is empty, redirection to all users
        this.$router.push({
          name: 'users'
        });
        this.$refs.infiniteLoading.stateChanger.reset();
      }
    },
    closeModal() {
      // eslint-disable-next-line vue/no-mutating-props
      this.showConfig.showNewUserForm = false;
    }
  }
};